<template>
  <div>
    <a-tabs
      hideAdd
      :activeKey="workflowActiveKey"
      type="editable-card"
      @edit="onEdit"
      style="padding:10px 10px 0 10px;"
      class="tabtilt"
      @change="routerchange"
    >
      <a-tab-pane
        v-for="(pane,i) in workflowPanes"
        :tab="pane.title"
        :key="i"
        :closable="pane.close"
      >
        <edit v-if="pane.type==1" ref="edit" :wftId="pane.id"></edit>
        <list v-else></list>
      </a-tab-pane>
    </a-tabs>

    <!-- <router-view :key="key"></router-view> -->
  </div>
</template>

<script>
import { mapState } from "vuex";
import edit from "./edit";
import list from "./list";
export default {
  components: {
    edit,
    list
  },
  data() {
    return {
      key: ""
    };
  },
  computed: {
    ...mapState(["workflowActiveKey", "workflowPanes"])
  },
  mounted() {
    console.warn("tabs mounted");
    console.log(this.$route.query, "tabs mounted");
    this.$store.dispatch("initPane", this.$route.query);
  },
  methods: {
    onEdit(targetKey, action) {
      console.warn("on edit");
      // return;
      if (action == "remove") this[action](targetKey);
    },
    remove(targetKey) {
      console.log("remove", targetKey);
      this.$store.dispatch("Workflowremove", targetKey);
    },
    routerchange(workflowActiveKey) {
      this.$store.dispatch("WorkflowRouterchange", workflowActiveKey);
    }
  },
  watch: {
    $route(to, from) {
      //监听路由是否变化
      //  console.log(from.query.wftId);
      if (to.query != from.query) {
        console.warn("watch");
        console.log(to.query);
        this.key = new Date().getTime();
      }
    }
  }
};
</script>

<style lang="less" scoped>
</style>




